import "../App.css";

export default function About() {
  return (
    <section class="section is-fullheight is-fullheight-with-navbar" id="about">
      <div class="container">
        <div class="section-heading">
          <h1 class="title is-2">About Me</h1>
          <h2 class="subtitle is-4">
            <div class="columns">
              <div class="column">
                <figure class="image">
                  <img alt="" src={process.env.PUBLIC_URL + "/head.jpg"} />
                </figure>
              </div>
              <div class="column">
                <p align="justify">
                My name is Krijan Prajapati. I am a final year Software Engineering student with a sub-major in Data Analytics. My expertise is in finding trends and shaping recommendations using data analytics.    
                </p> 
                <br></br>
                <p align="justify">
                From my early university studies, I was intrigued by data and how data can be used to improve the workflow of large organizations. Through studying engineering, I gained knowledge of understanding and examining the areas where we can integrate data analytics to create a change.
                </p>
                <br></br>
                <p align="justify">
                I am currently working for Commonwealth Bank of Australia as Machine Learning Engineer and using cloud technology for the work process.
                </p>
                <br/>
                <a
                  class="button is-dark"
                  href={
                    process.env.PUBLIC_URL + "/Krijan_Resume.pdf"
                  }
                  target="_blank"
                  rel="noreferrer noopener"
                >
                  View Resume
                </a>
              </div>
            </div>
          </h2>
        </div>
      </div>
    </section>
  );
}
